import Vue from 'vue'
import Router from 'vue-router'
import Login from './components/Login.vue'
// 登录页
import Home from './components/Home.vue'
import Welcome from "./components/Welcome.vue"
//异常报警
import Abnormal from './components/Abnormal/abnormal.vue'
// 实时数据
import Isdata from "./components/IsData/data.vue"
//数据管理数据报表
import DataManagedataFrom from "./components/DataManage/dataFrom.vue"
//设备配置门禁管理EqLayout
import EqLayoutDoor from "./components/EqLayout/door.vue"
//设备配置体温测量
import EqLayoutHeat from "./components/EqLayout/heat.vue"
//设备配置人员识别
import EqLayoutMan from "./components/EqLayout/man.vue"
//设备配置口罩检测
import EqLayoutMouth from "./components/EqLayout/mouth.vue"
//设备配置考勤管理
import EqLayoutwork from "./components/EqLayout/work.vue"
//设备管理设备日志
import EqManageLog from "./components/EqManage/log.vue"
//设备管理设备信息
import EqManageMessage from "./components/EqManage/message.vue"
//设备管理识别记录
import EqManageRecord from "./components/EqManage/record.vue"
//人事管理人员信息
import ManManageMessage from "./components/ManManage/manmessage.vue"
//人事管理角色管理
import ManManageRoles from "./components/ManManage/roles.vue"
// 人事管理企业信息
import ManManageCompany from "./components/ManManage/company.vue"
// 视频预览
import isVideo from "./components/video/isVideo.vue"


Vue.use(Router)
const router = new Router({
    mode: 'history',
    routes: [
        { path: '/', redirect: '/login' },
        { path: '/login', component: Login },

        {
            path: '/home',
            component: Home,
            name: 'Home',
            meta: [
                { name: '首页' }
            ],
            redirect: "/eqmanagemessage",
            children: [{
                path: "/welcome",
                component: Welcome,
                name: 'Welcome',
                meta: [
                    { name: '首页' }
                ]
            }, {
                path: "/isvideo",
                component: isVideo,
                name: 'isVideo',
                meta: [
                    { name: '首页' }, //{ name: '首页', url: '/welcome' },
                    { name: '视频预览' }
                ]
            }, {
                path: "/manmanagemessage",
                component: ManManageMessage,
                name: 'ManManageMessage',
                meta: [
                    { name: '首页' }, //{ name: '首页', url: '/welcome' },
                    { name: '人事管理' },
                    { name: '人员信息' }
                ]
            }, {
                path: "/manmanagecompany",
                component: ManManageCompany,
                name: 'ManManageCompany',
                meta: [
                    { name: '首页' }, //{ name: '首页', url: '/welcome' },
                    { name: '人事管理' },
                    { name: '企业信息' }
                ]
            }, {
                path: "/manmanageroles",
                component: ManManageRoles,
                name: 'ManManageRoles',
                meta: [
                    { name: '首页' }, //{ name: '首页', url: '/welcome' },
                    { name: '人事管理' },
                    { name: '角色管理' }
                ]
            }, {
                path: "/eqmanagemessage",
                component: EqManageMessage,
                name: 'EqManageMessage',
                meta: [
                    { name: '首页' },
                    { name: '设备管理' }
                ]
            }, {
                path: "/eqlayoutman",
                component: EqLayoutMan,
                name: 'EqLayoutMan',
                meta: [
                    { name: '首页' },
                    { name: '设备配置' },
                    { name: '人员识别' }
                ]
            }, {
                path: "/eqlayoutmouth",
                component: EqLayoutMouth,
                name: 'EqLayoutMouth',
                meta: [
                    { name: '首页' },
                    { name: '设备配置' },
                    { name: '口罩检测' }
                ]
            }, {
                path: "/eqlayoutheat",
                component: EqLayoutHeat,
                name: 'EqLayoutHeat',
                meta: [
                    { name: '首页' },
                    { name: '设备配置' },
                    { name: '体温测量' }
                ]
            }, {
                path: "/eqLayoutdoor",
                component: EqLayoutDoor,
                name: 'EqLayoutDoor',
                meta: [
                    { name: '首页' },
                    { name: '设备配置' },
                    { name: '门禁管理' }
                ]
            }, {
                path: "/eqlayoutwork",
                component: EqLayoutwork,
                name: 'EqLayoutwork',
                meta: [
                    { name: '首页' },
                    { name: '设备配置' },
                    { name: '考勤管理' }
                ]
            }, {
                path: "/eqmanagerecord",
                component: EqManageRecord,
                name: 'EqManageRecord',
                meta: [
                    { name: '首页' },
                    { name: '数据管理' },
                    { name: '识别记录' }
                ]
            }, {
                path: "/eqmanagelog",
                component: EqManageLog,
                name: 'EqManageLog',
                meta: [
                    { name: '首页' },
                    { name: '数据管理' },
                    { name: '操作日志' }
                ]
            }, {
                path: "/datamanagedatafrom",
                component: DataManagedataFrom,
                name: 'DataManagedataFrom',
                meta: [
                    { name: '首页' },
                    { name: '数据分析' }
                ]
            }]
        }
    ]
})

router.beforeEach((to, from, next) => {
    next()



})

export default router