<template>
  <div class="app">
    <el-container direction="horizontal">
      <el-aside width="200px">
        <div class="home-box">
          <div class="home-logo"><img :src="this.configGd.logo_home" /></div>
          <div class="home-logo_title">{{this.configGd.apptitle}}</div>
        </div>

        <!-- 菜单导航 -->
        <div class="home-menu">
          <el-menu
            unique-opened
            style="border-right: 0"
            background-color="#062a4b"
            text-color="#909cc0"
            active-text-color="#fff"
            :default-active="activeIndex"
            @select="handleMenuSelect"
            router
          >
            <NavMenu :navMenus="menuData"></NavMenu>
          </el-menu>
        </div>
      </el-aside>

      <el-container direction="vertical">
        <!-- 头部区域 -->
        <el-header height="98px">
          <div class="header-topbar">
            <i><img  class="topbar-face" src="../assets/img/userface.png" /></i>
            <span class="topbar-name">{{isName}}</span>
            <span class="topbar-role" v-if="isAuth && isAuth[0] && isAuth[0].authority_description"
            >({{isAuth[0].authority_description}})</span>
            <el-button type="danger" @click="logout" class="b_logout" icon="el-icon-circle-close">退出</el-button>
          </div>

          <!-- 面包屑导航 -->
          <div class="home_breadcrumb">
            <Mbreadcrumb :currentPath="breads"></Mbreadcrumb>
          </div>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>

        <!-- 版权区域 -->
        <el-footer height="40px">
          <div class="div_inline">
            <span class="copyright_font">{{this.configGd.copyright}}</span>
          </div>
          <div class="div_inline div_right">
            <span class="font_version">v {{this.configGd.version}}</span>
          </div>
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import menu from './menu.json'
import NavMenu from './NavMenu.vue'
import Mbreadcrumb from './Mbreadcrumb.vue'

// var data = menu
export default {
  components: {
    NavMenu: NavMenu,
    Mbreadcrumb: Mbreadcrumb
  },
  data() {
    return {
      activeIndex: 'eqmanagerecord',
      menuData: [],
      breads: [],
      isToken: '',
      isName: '',
      isRouteName: '',
      isAuth: ''
    }
  },
  created() {
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    this.isName = JSON.parse(sessionStorage.getItem('usermessage'))
    this.isAuth = JSON.parse(sessionStorage.getItem('auth'))
    //this.getLimit()
  },
  mounted() {
    this.menuData = menu
    this.isRouteName = this.$route.path
  },
  destroyed() {
    this.isAdmin = JSON.parse(sessionStorage.getItem('isAdmin'))
    this.userMessage = JSON.parse(sessionStorage.getItem('userMessage'))
  },
  methods: {
      getLimit() {
      this.$http
        .get(
          '/api' +
            '/device/find/limit',
          {
            headers: {
              'Content-Type': 'application/json'
            }
          }
        )
        .then(res => {
          console.log('res.data.data:', res.data.data)
          if (res.data && res.data.code == 0 && res.data.data) {
            console.log('res.data.data.lt', res.data.data.lt)
            console.log('res.data.data.gt:', res.data.data.gt)
            this.configGd.limit = [res.data.data.lt, res.data.data.gt]
          }
          console.log('this.configGd.limit:', this.configGd.limit)
        })
        // eslint-disable-next-line handle-callback-err
        .catch(err => {
          console.log('查询体温报警阈值失败')
        })
    },
    logout() {
      this.$http
        .get('/api' + '/user/signOut', {
          headers: {
            'Content-Type': 'application/json',
            token: this.isToken
          }
        })
        .then(res => {
          this.$router.push('/login')
        })
        .catch(err => {
          console.log(err)
        })
    },
    isapi() {
      this.$router.push('/api')
    },

    triggermain(url) {
      this.$router.push(url)
      // 点击切换的时候跳转.自动刷新
      // this.$router.go(0)
    },
    enter() {
      this.isMoust = true
    },
    leave() {
      this.isMoust = false
    },
    handleMenuSelect(index, indexPath) {
      this.breads = indexPath
    }
  },
  watch: {}
}
</script>

<style lang="less" scoped>

.el-aside {
  width: 200px; //0.45rem
  background: #0d3962;
}
.el-main {
  background-color: #eaedf2;
  min-width: 0;
  min-height: calc(100vh - 138px); //138px
  padding: 5px 20px 2px 20px
}
.el-header {
  min-width: 0;
  background-image: url('../assets/img/topbarbg.png');
  height: 98px;
}
.el-footer {
  min-width: 0;
  background-color: #fff;
  height: 40px;
  font-size: 13px;
  padding: 8px 0px 0px 16px;
}
.b_logout {
  width: 76px;
  height: 32px;
  float: right;
  margin-top: -2px;
  padding: 0px 1px 0px 0px;
}
.home-box {
  width: 200px; //0.45rem
  height: 98px; //0.45rem
  background-image: url('../assets/img/logobg.png');
  .home-logo {
    width: 200px;
    height: 48px;
    text-align:center;
    padding-top: 11px;
    img {
      width: 48px;
      height: 48px;
    }
  }
  .home-logo_title {
    width: 200px; //0.45rem
    color: #fff;
    font-size: 18px;
    font-weight: 900px;
    text-align: center;
    padding-top: 4px;
  }
}
.home-menu {
  width: 200px;
}

.header-topbar {
  height: 60px;
  margin-left: 0px;
  margin-top: 17px;
  .topbar-face {
    margin-bottom: 4px;
  }
  .topbar-name {
    color: #3c4e86;
    margin-left: 6px;
    font-size: 17px;
    font-weight: 600;
  }
  .topbar-role {
    color: #3c4e86;
    margin-left: 5px;
    font-size: 14px;
  }
}
// 面包屑导航
.home_breadcrumb {
  height: 38px;
  margin-left: 0px; //2.25rem
  margin-top: -4px;
}

.el-menu {
  border-right: 0 !important;
}
</style>
